<template>
  <div class="d-flex demo">
    <div v-if="scene.children.length > 0">
      <Container
        behaviour="contain"
        orientation="horizontal"
        @drop="onColumnDrop($event)"
        drag-handle-selector=".column-drag-handle"
        @drag-start="dragStart"
        :drop-placeholder="upperDropPlaceholderOptions">
        <Draggable v-for="(column, indexCard) in scene.children" :key="indexCard">
          <v-card 
            width="320" 
            class="mb-6"
            :class="{'mr-4': scene.children.length === indexCard+1?false:true}">
            <v-card-title 
              class="py-1 purple lighten-4 font-weight-regular subtitle-1 column-drag-handle">
              {{ column.name }}
            </v-card-title>
            <v-card-text class="pa-3 pb-0 grey lighten-3 card-container">
              <Container
                group-name="col"
                @drop="(e) => onCardDrop(indexCard, e)"
                @drag-start="(e) => log('drag start', e)"
                @drag-end="(e) => log('drag end', e)"
                :get-child-payload="getCardPayload(indexCard)"
                drag-class="card-ghost"
                drop-class="card-ghost-drop"
                :drop-placeholder="dropPlaceholderOptions">
                <Draggable v-for="(card, i) in column.children" :key="i">
                  <v-sheet class="pa-3 mb-3">
                    <p>{{ card.data }}</p>
                  </v-sheet>
                </Draggable>
              </Container>
            </v-card-text>
            <v-card-actions class="grey lighten-3">
              <TambahKegiatan 
                :kegiatan="column" 
                @kegiatan="tambahKegiatan" 
              />
            </v-card-actions>
          </v-card>
        </Draggable>
      </Container>
    </div>
    <div :class="{'ml-4': scene.children.length > 0?true:false}">
      <v-card>
        <v-form v-model="valid" ref="form">
          <v-card-text class="pb-0">
            <v-text-field
              outlined
              :rules="requiredRule"
              v-model="dataProgress.nama"
              dense
              autocomplete="off"
              placeholder="Nama list"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-btn
              type="submit"
              :disabled="!valid"
              @click.prevent="validate"
              depressed
              width="320"
              color="cyan"
              class="white--text">
              <v-icon class="mr-3">mdi-plus-circle</v-icon>
              tambah list
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </div>
  </div>  
</template>

<style>
  .demo{
    padding: 8px;
    overflow: auto;
    min-width: 0;
  }
  .card-container{
    max-height: 500px;
    overflow-y: auto; 
  }
  .column-drag-handle{ cursor: move; }
  .card-ghost {
    transition: transform 0.18s ease;
    transform: rotateZ(5deg)
  }
  .card-ghost-drop {
    transition: transform 0.18s ease-in-out;
    transform: rotateZ(0deg)
  }
  .drop-preview {
    background-color: rgba(150, 150, 200, 0.1);
    border: 1px dashed #abc;
    margin-bottom: 16px;
  }
  .cards-drop-preview {
    background-color: rgba(150, 150, 200, 0.1);
    border: 1px dashed #abc;
    margin-right: 16px;
  }
</style>

<script>
import { Container, Draggable } from 'vue-smooth-dnd'
// import { applyDrag, generateItems } from '@/utils/helpers'
import { applyDrag } from '@/utils/helpers'
import TambahKegiatan from './TambahKegiatan'
// const lorem = `Lorem ipsum dolor sit amet`
// const scene2 = {
//   type: 'container',
//   props: {
//     orientation: 'horizontal'
//   },
//   children: generateItems(1, i => ({
//     // id: `column${i}`,
//     type: 'draggable',
//     name: 'gg',
//     props: {
//       orientation: 'vertical'
//     },
//     children: generateItems(2, j => ({
//       type: 'draggable',
//       id: `${i}${j}`,
//       data: lorem
//     }))
//   }))
// }
const scene = {
  type: 'container',
  props: {
    orientation: 'horizontal'
  },
  children: []
}
export default {
  components: {
    Container, 
    Draggable,
    TambahKegiatan
  },
  data () {
    return {
      scene,     
      valid: true,
      dataProgress: {
        nama: ''
      },
      requiredRule: [v=>!!v || 'Harus di isi'], 
      upperDropPlaceholderOptions: {
        className: 'cards-drop-preview',
        animationDuration: '150',
        showOnTop: true
      },
      dropPlaceholderOptions: {
        className: 'drop-preview',
        animationDuration: '150',
        showOnTop: true
      }
    }
  },
  methods: {
    onColumnDrop (dropResult) {
      const scene = Object.assign({}, this.scene)
      scene.children = applyDrag(scene.children, dropResult)
      this.scene = scene
    },
    onCardDrop (columnId, dropResult) {
      if (dropResult.removedIndex !== null || dropResult.addedIndex !== null) {
        const scene = Object.assign({}, this.scene)
        const column = scene.children.filter(p => p.id === columnId)[0]
        const columnIndex = scene.children.indexOf(column)
        const newColumn = Object.assign({}, column)
        newColumn.children = applyDrag(newColumn.children, dropResult)
        scene.children.splice(columnIndex, 1, newColumn)
        this.scene = scene
      }
    },
    getCardPayload (columnId) {
      return index => {
        return this.scene.children.filter(p => p.id === columnId)[0].children[index]
      }
    },
    dragStart () {
      console.log('drag started')
    },
    log (...params) {
      console.log(...params)
    },
    validate() {
      if (this.$refs.form.validate()) {
        let id = this.scene.children.length
        this.scene.children.push({
          type: 'draggable',
          id: id,
          name: this.dataProgress.nama,
          props: {
            orientation: 'vertical'
          },
          children: []
        })
        // this.dataProgress.nama = ''
      }
    },
    tambahKegiatan(val) {
      this.scene.children[val.index].children.push(val.data)
    }
  }
}
</script>