<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn
        text v-on="on"
        block
        color="cyan">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        tambah kegiatan
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Tambah Kegiatan</span>
      </div>
      <v-form v-model="valid" ref="form" enctype="multipart/form-data">
        <v-card-text>
          <v-text-field
            v-model="dataKegiatan.kegiatan"
            label="Name"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>    
          <v-file-input
            accept="image/*"
            v-model="dataKegiatan.gambar"
            show-size
            counter
            label="Gambar"
          ></v-file-input>   

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  export default {
    props: ['kegiatan'],

    data () {
      return {
        dialog: false,
        valid: true,
        dataKegiatan: {
          kegiatan: '',
          gambar: null
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataIncubatorMentor', ['dataMentor', 'isLoading', 'status']),      
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          // this.$store.dispatch('dataIncubatorMentor/postDataMentor', data)
          // this.$store.dispatch('dataIncubatorMentor/updateIsLoading', true)
          // this.msg.visible = true
          let id = this.kegiatan.children.length
          this.$emit('kegiatan', {
            data: {
              type: 'draggable',
              id: id,
              data: this.dataKegiatan.kegiatan
            },
            index: this.kegiatan.id
          })
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorMentor/updateStatus', null)
      }
    }
  }
</script>